<template>
	<view>
		<!-- 登录显示 -->
		<view v-if="token !== ''">
			<view class="contain">
				<image @click="changeBgImg()" :src="comBgImg" mode="aspectFill"></image>
				<view class="avatar flex justify-between">
					<view class="text-center padding flex align-center">
						<image @click="editUserInfo()" :src="userInfo.avatar" mode="aspectFill"></image>
						<view class="fs-34 margin-left text-center text-bold">
							{{userInfo.account}}
							<text @click="accountShow=true"
								class="xiala margin-left-xs color-fff cuIcon-triangledownfill"></text>
							<!-- 切换账号弹出窗口 -->
							<u-popup :show="accountShow" @close="accountShow = false">
								<view class="popup-info">
									<view class="accountActive padding flex justify-between align-center">
										<view class="flex align-center">
											<image :src="userInfo.avatar" mode="aspectFill"></image>
											<view class="padding-left-sm">
												<text class="color-fff">{{userInfo.account}}</text>
												<view class="text-left">
													<text class="fs-26 color-huise">待收货0收藏0</text>
												</view>
											</view>
										</view>
										<radio color="red" checked="true" />
									</view>
									<view @click="addAccount()" class="padding text-left flex align-center">
										<view class="accountAdd  margin-right">
											<text class=" cuIcon-add color-huise"></text>
										</view>
										<text class="color-fff fs-30">添加或切换账号</text>
									</view>
								</view>
							</u-popup>
							<view class="fs-20 color-huise text-left">
								昵称:{{userInfo.nickname}}
							</view>
						</view>
					</view>
				</view>
				
				<view class="setting flex padding">
					<view class="padding-right-sm margin-right">
						<u-icon size="22" color="black" name="kefu-ermai"></u-icon>
					</view>
				
					<view @click="editUserInfo">
						<u-icon size="22" color="black" name="setting"></u-icon>
					</view>
				</view>
			</view>

			<view class="context">
				<view class="padding-xl">
					<!-- 收藏 -->
					<view class="fillet padding-bottom bg-huise margin-bottom">
						<u-grid :border="false">
							<u-grid-item v-for="(item,i) in baseList" :key="i">
								<u-icon :customStyle="{paddingTop:20+'rpx'}" :name="item.name" color="#66bbad"
									:size="22">
								</u-icon>
								<text class="fs-26">{{item.title}}</text>
							</u-grid-item>
						</u-grid>
					</view>

					<!-- 宫格 -->
					<view class="fillet grids bg-huise">
						<u-grid :border="false">
							<u-grid-item @click="clickMyorder(i)" v-for="(item,i) in orderList" :key="i">
								<u-icon :customStyle="{paddingTop:20+'rpx'}" :name="item.name" color="#66bbad"
									:size="22">
								</u-icon>
								<text class="fs-26">{{item.title}}</text>
							</u-grid-item>
						</u-grid>
					</view>
				</view>

				<view class="margin bg-fff">
					<u-cell-group>
						<u-cell icon="map" title="收货地址" :isLink="true" arrow-direction="left" size='large'></u-cell>
					</u-cell-group>
				</view>


				<view class="padding btn-info">
					<u-button @click="loginOut()" text="退出登录" :plain="true" shape="circle" color="#66bbad"
						:hairline="true">
					</u-button>
				</view>
			</view>
		</view>


		<!-- 未登录显示 -->
		<view v-else>
			<u-empty mode="permission" icon="http://cdn.uviewui.com/uview/empty/permission.png">
			</u-empty>
		</view>

		<!-- 退出登录提示 -->
		<u-modal @confirm="logOutConfirm" @cancel="logOutShow = false" :show="logOutShow" :showCancelButton="true"
			cancelColor="#fed68a" confirmColor='#66bbad' title="退出登录" content='确定退出吗？'></u-modal>
		<!-- 如果没登录 提示去登录 -->
		<u-modal @confirm="loginConfirm" @cancel="loginCancel" :show="loginShow" :showCancelButton="true"
			cancelText='返回首页' confirmText="去登录" cancelColor="#fed68a" confirmColor='#66bbad' title="请先登录"
			content='去登录？'></u-modal>
	</view>
</template>

<script>
	import {
		getUserInfo
	} from '../../api/user.js'
	import {
		mapState
	} from 'vuex'
	import {
		getOrder
	} from '../../api/order.js'
	import {
		getStorage
	} from "../../utils/persistence.js"
	export default {
		data() {
			return {
				bgImg: ['../../static/img/y.jpg'],
				userInfoObj: {},
				accountShow: false,
				logOutShow: false,
				loginShow: false,
				baseList: [{
						name: 'heart-fill',
						title: '我的收藏'
					},
					{
						name: 'file-text-fill',
						title: '退款售后'
					},

					{
						name: 'eye-fill',
						title: '我的足迹'
					}
				],
				orderList: [{
					name: 'calendar',
					title: '待付款'
				}, {
					name: 'car',
					title: '待发货'
				}, {
					name: 'clock',
					title: "待收货"
				}, {
					name: "file-text",
					title: '待评价'
				}, {
					name: "coupon",
					title: '已完成'
				}, {
					name: 'minus-circle',
					title: '已取消'
				}],
				orderObj: {
					page: 1,
					pageSize: 10,
					orderState: 0
				}
			};
		},
		onLoad() {
			// 页面加载时获取token并判断是否存在
			// 不存在即未登录 提示去登录
			let token = this.$store.state.user.token
			if (token === '') {
				this.loginShow = true
			}
			this.getUserInfo()
			console.log(this.userInfo);
		},
		methods: {
			// 用户信息
			async getUserInfo() {
				const res = await getUserInfo()
				console.log(res);
				this.userInfoObj = res.data.result
			},
			// 编辑用户信息
			editUserInfo() {
				uni.navigateTo({
					url: '/pages/user/userInfo',
					success: (res) => {
						res.eventChannel.emit('item', {
							data: this.userInfoObj
						})
					}
				})
			},
			changeBgImg(){
				uni.previewImage({
					urls:this.bgImg,
					longPressActions:{
						itemList:['保存图片'],
						success:(res) => {
							console.log(res);
							let imgurl = this.bgImg.toString()
							this.saveImage(imgurl)
						}
					}
				})
			},
			saveImage(imgurl){
				uni.downloadFile({
					url:imgurl,
					success: (res) => {
						console.log(res);
						let url = res.tempFilePath
						uni.saveImageToPhotosAlbum({
							filePath:url,
							success: () => {
								uni.showToast({
									title:'保存成功',
									icon:'none'
								})
							},
							fail: (err) => {
								uni.showToast({
									title:'保存失败',
									icon:'none'
								})
							}
						})
					}
				})
			},
			// 添加或切换账号
			addAccount(){
				uni.navigateTo({
					url:'/pages/user/login'
				})
			},
			// 我的订单
			async clickMyorder(i) {
				this.orderObj.orderState = i + 1
				const res = await getOrder(this.orderObj)
				console.log(res);
			},
			// 退出登录
			loginOut() {
				this.logOutShow = true
			},
			// 确定退出
			logOutConfirm() {
				this.$store.commit('user/removeLogin')
				this.logOutShow = false
			},
			// 去登录
			loginConfirm() {
				uni.redirectTo({
					url: '/pages/user/login'
				})
			},
			// 返回首页
			loginCancel() {
				uni.switchTab({
					url: '/pages/home/home'
				})
			}
		},
		computed: {
			...mapState('user', ['userInfo', 'token']),
			comBgImg(){
				return this.bgImg.toString()
			}
		}
	}
</script>

<style lang="scss">
	.contain {
		width: 100%;

		// height: 300rpx;
		image {
			width: 100%;
			height: 350rpx;
		}
	}

	.grid .iconfont {
		font-size: 60rpx;
		color: #888;
	}

	.avatar {
		width: 100%;
		position: absolute;
		top: 130rpx;
		padding: 20rpx 0 20rpx 0;

		image {
			width: 150rpx;
			height: 150rpx;
			border-radius: 50%;
		}

		.xiala {
			width: 50rpx;
			height: 50rpx;
			background-color: #847e74;
			border-radius: 50%;
		}

	}
	.setting{
		position: absolute;
		top: 50rpx;
		right: 0;
		view{
			width: 65rpx;
			height: 65rpx;
			background-color: #9e9689;
			border-radius: 50%;
		}
	}
	::v-deep .uicon-setting{
		margin-left: 10rpx;
	}
	::v-deep .uicon-kefu-ermai{
		margin-left: 10rpx;
	}

	.context {
		position: absolute;
		top: 380rpx;
		width: 100%;
		background-color: #fff;
		border-radius: 30rpx;
	}

	.userInfo-con {
		width: 600rpx;
	}

	.grids {
		height: 220rpx;
		background-color: #fff;
	}

	// 圆角
	.fillet {
		border-radius: 20rpx;
	}

	// 切换账号弹出窗口
	.popup-info {
		width: 100%;
		height: 320rpx;
		background-color: #151724;
		border-radius: 20rpx 20rpx 0 0;

		.accountActive {
			image {
				width: 100rpx;
				height: 100rpx;
				border-radius: 50%;
			}
		}
		.accountAdd{
			display: flex;
			width: 100rpx;
			height: 100rpx;
			background-color: #3b3b43;

			border-radius: 50%;
			text{
				margin: auto;
				font-size: 50rpx;
			}
		}
	}
</style>
